import React, { Fragment, useState } from "react";
import "../../styles/features.css";
import FeatureAutoPlay from "./FeatureAutoPlay";
import Plus from "../../icons/plus/plus.svg";
import Minus from "../../icons/minus/minus.svg";
import Expand from "../../icons/expand/expand_icon.svg";

function FeatureAccordion({
  accordion,
  id,
  open,
  onClick,
  screen,
  isVisible,
  isClick,
  setIsClick,
  isDesktop,
  isDesktopMd,
  handlePopUpClick,
}) {
  const [isClose, setIsClose] = useState(!open);

  const accordionClick = (id) => {
    onClick(id);
    setIsClick(true);
  };

  const closeClick = () => {
    // onClick(id);
    setIsClose(!isClose);
  };

  // useEffect(() => {
  //   const timer = setTimeout(() => {
  //     setIsClick(false);
  //   }, 5000);
  //   return () => clearTimeout(timer);
  // }, [isClick]);

  const titleStyle = open ? { color: "#1B1C1D" } : { color: "#939595" };

  // const titleNewStyle = !isClose ? { color: "#1B1C1D" } : { color: "#939595" };

  const indicatorStyle = !open
    ? {
        marginLeft: "2px",
        // marginRight: "2px",
        minWidth: "2px",
        maxWidth: "2px",
      }
    : isClick
    ? {
        marginLeft: "0px",
        marginRight: "0px",
        minWidth: "4px",
        maxWidth: "4px",
      }
    : {
        marginLeft: "2px",
        // marginRight: "2px",
        minWidth: "2px",
        maxWidth: "2px",
        backgroundColor: "#E6E7E8",
      };

  // const indicatorNewStyle = !isClose
  //   ? {
  //       marginLeft: "2px",
  //       // marginRight: "2px",
  //       minWidth: "2px",
  //       maxWidth: "2px",
  //     }
  //   : !isClose
  //   ? {
  //       marginLeft: "0px",
  //       marginRight: "0px",
  //       minWidth: "4px",
  //       maxWidth: "4px",
  //     }
  //   : {
  //       marginLeft: "2px",
  //       // marginRight: "2px",
  //       minWidth: "2px",
  //       maxWidth: "2px",
  //       backgroundColor: "#00bceb",
  //     };

  const cardStyle = open
    ? { overflow: "hidden", maxHeight: "400px" }
    : { maxHeight: "64px" };

  const cardNewStyle = !isClose
    ? { overflow: "hidden", maxHeight: "400px" }
    : { maxHeight: "64px" };

  let show;
  if (isDesktop || isDesktopMd) {
    show = (
      <Fragment className="Feature-sub-container">
        <div className="Feature-Accordion-container">
          <div className="Feature-Accordion-indicator" style={indicatorStyle}>
            {isClick === false && open && (
              <FeatureAutoPlay
                height={100}
                isVisible={isVisible}
                // isDesktop={isDesktop}
                // isDesktopMd={isDesktopMd}
              />
            )}
          </div>
          <div
            style={cardStyle}
            className="Feature-Accordion-card"
            onClick={() => accordionClick(id)}
          >
            <div className="Feature-Accordion-title" style={titleStyle}>
              {accordion.title}
            </div>
            {open && (
              <div className="Feature-Accordion-content">
                {accordion.content}
              </div>
            )}
          </div>
        </div>
        {/* {open && screen && (
          <img
            className="carousel-image"
            src={accordion.image}
            alt="carousel"
          />
        )} */}
      </Fragment>
    );
  } else {
    show = (
      <Fragment className="Feature-sub-container">
        <div
          className="Feature-Accordion-container"
          style={
            !isClose
              ? { borderBottom: "none" }
              : { borderBottom: "1px solid #e6e7e8" }
          }
        >
          {/* <div
            className="Feature-Accordion-indicator"
            style={indicatorNewStyle}
          >
            {!isClose && (
              <FeatureAutoPlay
                height={100}
                isVisible={isVisible}
                isDesktop={isDesktop}
                isDesktopMd={isDesktopMd}
              />
            )}
          </div> */}
          <div
            style={cardNewStyle}
            className="Feature-Accordion-card"
            onClick={() => closeClick()}
          >
            <div className="Feature-Accordion-title-container">
              <span className="Feature-Accordion-title">{accordion.title}</span>
              <span style={{ alignSelf: "center" }}>
                {!isClose ? (
                  <img src={Minus} alt="minus" />
                ) : (
                  <img src={Plus} alt="plus" />
                )}
              </span>
            </div>

            {!isClose && (
              <div className="Feature-Accordion-content">
                {accordion.content}
              </div>
            )}
          </div>
        </div>
        {!isClose && screen && (
          <div
            className="feature-carousel-container-small-screen"
            onClick={handlePopUpClick}
          >
            <div className="feature-carousel-expand">
              <img src={Expand} alt="expand" />
            </div>

            <img
              className="carousel-image"
              src={accordion.image}
              alt="carousel"
            />
          </div>
        )}
      </Fragment>
    );
  }

  return <Fragment>{show}</Fragment>;
}

export default FeatureAccordion;



