import React from "react";
import "../../styles/features.css";
import close from "../../icons/X/icLG-close.svg";
import prev from "../../icons/arrows/prev.svg";
import next from "../../icons/arrows/next.svg";
import styled from "styled-components";

function CarouselPopUp({
  setIsPopUp,
  image,
  content,
  id,
  onPrev,
  onNext,
  handleIndicatorClick,
  featureAccordion,
}) {
  const handleClose = () => {
    setIsPopUp(false);
  };

  const prevCarousel = id === 1 ? "" : <img src={prev} alt="prev" />;
  const nextCarousel = id === 4 ? "" : <img src={next} alt="next" />;

  const Span = styled.span`
    & {
      height: 8px;
      width: 8px;
      display: flex;
      align-items: center;
      cursor: pointer;
    }

    &::before {
      content: "";
      height: 6px;
      width: 6px;
      background-color: #c3c4c5;
      border-radius: 20px;
      transition: background-color 0.3s ease;
    }

    &:hover::before {
      background-color: #54bbe6;
    }

    &[data-dot="${id}"]::before {
      background-color: #54bbe6;
    }
  `;

  return (
    <div className="Feature-Accordion-popup-container-outer">
      <div className="Feature-Accordion-popup-container-inner">
        <div className="popup-close-tag">
          <img src={close} alt="close" onClick={handleClose} />
        </div>
        <div className="popup-image-container">
          <div className="popup-image-container-left" onClick={onPrev}>
            {prevCarousel}
          </div>
          <div className="popup-image-container-middle">
            <img className="popup-image" src={image} alt="accordion" />
            <div className="popup-content">{content}</div>
          </div>
          <div className="popup-image-container-right" onClick={onNext}>
            {nextCarousel}
          </div>
        </div>
        <div className="popup-image-rectangle-container">
          {featureAccordion.map((_, i) => {
            return (
              <Span
                key={i}
                data-dot={i + 1}
                onClick={(e) => handleIndicatorClick(e)}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default CarouselPopUp;



