import React, { useState, useCallback, useEffect, Fragment } from "react";
import "../styles/licensing.css";
import GreaterThan from "../icons/12/greater_than.svg";
import LicensingCardOne from "./licensing/LicensingCardOne";
import LicensingCardTwo from "./licensing/LicensingCardTwo";
import LicensingCardThree from "./licensing/LicensingCardThree";
import LicensingCardFour from "./licensing/LicensingCardFour";
import UseFadeInHead from "./licensing/UseFadeInHead";
import AOS from "aos";
import "aos/dist/aos.css";

const useMediaQuery = (width) => {
  const [targetReached, setTargetReached] = useState(false);

  const updateTarget = useCallback((e) => {
    if (e.matches) {
      setTargetReached(true);
    } else {
      setTargetReached(false);
    }
  }, []);

  useEffect(() => {
    const media = window.matchMedia(`(min-width: ${width}px)`);
    media.addEventListener("change", (e) => updateTarget(e));

    // Check on mount (callback is not called until a change occurs)
    if (media.matches) {
      setTargetReached(true);
    }

    return () => media.removeEventListener("change", (e) => updateTarget(e));
  }, [updateTarget, width]);

  return targetReached;
};

function Licensing() {
  const isDesktop = useMediaQuery(1440);
  const isTablet = useMediaQuery(768);
  const isMobile = useMediaQuery(320);

  const { isHeadline, headerRef } = UseFadeInHead(200);

  useEffect(() => {}, []);

  AOS.init({
    // debounceDelay: 100,
    offset: 10,
    delay: 1000,
    easing: "ease-in",
    once: true,
    anchorrPlacement: "bottom-bottom",
  });

  let display;
  if (isDesktop) {
    display = (
      <div className="licensing-container">
        <div className="licensing-top">
          <div ref={headerRef} className="licensing-headline">
            Software subscription
          </div>
          <div className={isHeadline ? "is-visible" : "not-visible"}>
            <div className="licensing-launch">
              <div className="licensing-button">Help me choose</div>
              <div className="licensing-icon-greater">
                <img src={GreaterThan} alt="Greater Than" />
              </div>
            </div>
          </div>
        </div>
        <div className={isHeadline ? "is-visible" : "not-visible"}>
          <div className="licensing-middle">
            <LicensingCardOne
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
            <LicensingCardTwo
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
            <LicensingCardThree
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
            <LicensingCardFour
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
          </div>
        </div>
        <div className={isHeadline ? "is-visible" : "not-visible"}>
          <div className="licensing-bottom">
            <button className="licencing-btn">Compare all plans</button>
          </div>
        </div>
      </div>
    );
  } else if (isTablet || isMobile) {
    display = (
      <div className="licensing-container">
        <div className="licensing-top">
          <div className="licensing-headline">Software subscription</div>
          {/* <div className={isHeadline ? "is-visible" : "not-visible"}> */}
          <div className="licensing-launch" data-aos="fade-up">
            <div className="licensing-button">Help me choose</div>
            <div className="licensing-icon-greater">
              <img src={GreaterThan} alt="Greater Than" />
            </div>
          </div>
          {/* </div> */}
        </div>
        <div className="licensing-middle">
          <div data-aos="fade-up">
            <LicensingCardOne
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
          </div>
          <div data-aos="fade-up">
            <LicensingCardTwo
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
          </div>
          <div data-aos="fade-up">
            <LicensingCardThree
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
          </div>
          <div data-aos="fade-up">
            <LicensingCardFour
              isDesktop={isDesktop}
              isTablet={isTablet}
              isMobile={isMobile}
            />
          </div>
        </div>
        <div className="licensing-bottom" data-aos="fade-up">
          <button className="licencing-btn">Compare all plans</button>
        </div>
      </div>
    );
  }

  return <Fragment>{display}</Fragment>;
}

export default Licensing;



